import React from 'react';
import { DialogTitle, Dialog, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

export interface Props {
  onClose: Function;
  open: boolean;
}

const useStyles = makeStyles((theme) => ({
  dialog: {
    padding: 10,
    height: 500,
  },
  authButton: {
    height: 35,
    margin: '0px auto',
    width: 150,
    borderRadius: '6px',
    color: '#fff',
    fontSize: '16px',
    textTransform: 'capitalize',
    backgroundColor: '#11a683',
  },
}));

export const NotSignedInDialog: React.FC<Props> = ({ onClose, open }) => {
  const classes = useStyles();

  const handleClose = (event: any) => {
    onClose();
  };

  return (
    <Dialog
      className={classes.dialog}
      onClose={handleClose}
      aria-labelledby="dialog-title"
      open={open}
    >
      <DialogTitle id="dialog-title">You're not signed in yet</DialogTitle>
      <Button
        className={classes.authButton}
        size="medium"
        type="submit"
        variant="contained"
        onClick={(event: any) => handleClose(event)}
      >
        Continue
      </Button>
    </Dialog>
  );
};

export default NotSignedInDialog;
